.cards {
	border-top: 1px solid $card-border-color;
	border-left: 1px solid $card-border-color;
	font-size: 0; }

.card {
	border-right: 1px solid $card-border-color;
	border-bottom: 1px solid $card-border-color;
	padding: 22px 18px;
	// margin-left: -.36em

	@include hover {
			.card__title {
				color: $select-green;
				transition: 0.5s color; } }
	&__big {
		display: inline-block;
		width: 25%; }
	&__image {
		padding-bottom: 275px;
		@include cover;
		position: relative; }
	&__freemark {
		position: absolute;
		bottom: 0;
		right: 0;
		.icon {
			font-size: 3em;
			svg {
				height: 30px;
				width: 30px; } } }
	&__title {
		font-family: Roboto;
		font-size: 22px;
		font-weight: bold;
		font-style: normal;
		font-stretch: condensed;
		line-height: 1.09;
		text-align: center;
		color: $card-title-color;
		text-transform: uppercase;
		transition: 0.5s color; }

	&__price {
		font-family: CorpidE1sSCd;
		font-size: 21px;
		font-weight: bold;
		font-style: normal;
		font-stretch: normal;
		line-height: normal;
		letter-spacing: normal;
		text-align: center;
		color: $card-price-color; }
	&__id {
		font-family: CorpidE1sSCd;
		font-size: 21px;
		font-weight: bold;
		font-style: normal;
		font-stretch: normal;
		line-height: normal;
		letter-spacing: normal;
		text-align: center;
		&--id {
			color: $card-id-color-lighter; }
		&--number {
	  	color: $card-id-color-darker; } }
	&__divider {
		width: 100%;
		height: 4px;
		background: radial-gradient(4px 4px,$card-id-color-lighter 0%, $card-id-color-lighter 50%, transparent 50%);
		background-repeat: repeat-x;
		background-size: contain;
		background-size: 8px 4px;
		// border-bottom: 3px dotted $card-id-color-lighter
		margin: 16px auto; } }
