@include font('Roboto-Light', 'Roboto-Light');
@include font('Roboto-Regular', 'Roboto-Regular');
@include font('Roboto-Medium', 'Roboto-Medium');
@include font('RobotoCondensed-Bold', 'RobotoCondensed-Bold');
@include font('RobotoCondensed-Regular', 'RobotoCondensed-Regular');

@mixin rob-l {
	font-family: "Roboto-Light", Arial, Helvetica, sans-serif; }
@mixin rob-r {
	font-family: "Roboto-Regular", Arial, Helvetica, sans-serif; }
@mixin rob-m {
	font-family: "Roboto-Regular", Arial, Helvetica, sans-serif; }
@mixin rob-cb {
	font-family: "RobotoCondensed-Bold", Arial, Helvetica, sans-serif; }
@mixin rob-cr {
	font-family: "RobotoCondensed-Regular", Arial, Helvetica, sans-serif; }

//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

$corp: 'CorpidE1sSCd';

@font-face {
	@include fontdefault(normal, 400, $corp);
	@include fontpath('corpide1sscd-regular', 'corpide1sscd-regular', corpide1sscd-regular); }
@font-face {
	@include fontdefault(light, 300, $corp);
	@include fontpath('corpide1sscd-light', 'corpide1sscd-light', corpide1sscd-light); }
@font-face {
	@include fontdefault(normal, 700, $corp);
	@include fontpath('corpide1sscd-bold', 'corpide1sscd-bold', corpide1sscd-bold); }
@font-face {
	@include fontdefault(normal, 800, $corp);
	@include fontpath('corpide1sscd-heavy', 'corpide1sscd-heavy', corpide1sscd-heavy); }
@font-face {
	@include fontdefault(normal, 900, $corp);
	@include fontpath('corpide1sscd-black', 'corpide1sscd-black', corpide1sscd-black); }


body {
  position: relative;
  background: #FFF;
  line-height: 1.4;
  font-size: 17px;
  min-width: 320px;
  min-height: 1400px;
  overflow-x: hidden;
  @include rob-l; }


button {
  background: transparent; }


img {
  max-width: 100%; }


a {
  text-decoration: none; }
