.select2-container--default .select2-selection--single .select2-selection__arrow b {
    background: url(../assets/img/general/arr_green.png);
    background-size: cover;
    width: 14px;
    height: 9px;
    margin: 0 3px;
    border: none;
    left: -5px;
    top: 40%;
    transition: transform 0.3s; }
.select2-container--classic.select2-container--open .select2-selection--single .select2-selection__arrow b, .select2-container--default.select2-container--open .select2-selection--single .select2-selection__arrow b {
    transform: rotate(180deg);
    transition: transform 0.3s; }
.select2-selection__rendered,
.select2-results__option {
    font-family: $corp;
    font-size: 16px !important;
    line-height: 1.38;
    text-align: left;
    color: #3a5864;
    // padding-bottom: 4px
    padding-left: 8px; }
.select2-container .select2-selection--single {
    font-family: $corp;
    font-size: 16px;
    line-height: 1.38;
    text-align: left;
    color: #3a5864;
    height: 31px; }
