body {
  position: relative;
  background: #FFF;
  line-height: 1.4;
  font-size: 17px;
  min-width: 320px;
  min-height: 1400px;
  overflow-x: hidden;
  font-family: "Roboto-Light", Arial, Helvetica, sans-serif;
}

button {
  background: transparent;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

.inner {
  max-width: 1220px;
  width: 100%;
  margin: 0 auto;
  padding: 0 10px;
}

@media only screen and (max-width: 960px) {
  .inner {
    padding: 0 12px;
  }
}

.block {
  display: flex;
}

.header {
  position: relative;
}

.header.is-logined .links__item_logout {
  display: block;
}

.header__inner:after {
  content: " ";
  display: table;
  clear: both;
}

.header__logo {
  float: left;
  padding: 12px 0;
}

@media only screen and (max-width: 1130px) {
  .header__logo {
    float: none;
    text-align: center;
  }
}

@media only screen and (max-width: 960px) {
  .header__logo {
    padding-bottom: 0;
  }
}

.header__info {
  margin-top: 10px;
  float: right;
}

@media only screen and (max-width: 1130px) {
  .header__info {
    float: none;
    text-align: center;
  }
}

@media only screen and (max-width: 1023px) {
  .header__info {
    margin-bottom: 10px;
  }
}

.header__country {
  padding: 10px 20px;
  float: left;
}

@media only screen and (max-width: 1130px) {
  .header__country {
    padding-left: 0;
    float: none;
  }
}

.header__middle {
  border-top: 1px solid transparent;
}

@media only screen and (max-width: 960px) {
  .header__middle {
    border-top-color: #dba960;
  }
}

.header__locator {
  float: right;
}

@media only screen and (max-width: 1130px) {
  .header__locator {
    display: block;
    float: none;
  }
}

.header__bottom .inner {
  padding: 0 15px;
}

@media only screen and (max-width: 1130px) {
  .header__bottom .inner {
    padding: 0 10px;
  }
}

.header__logo-sm {
  display: inline-block;
  vertical-align: middle;
  padding: 5px 0 5px 20px;
}

@media only screen and (max-width: 1130px) {
  .header__logo-sm {
    padding-left: 0;
  }
}

@media only screen and (max-width: 960px) {
  .header__logo-sm {
    display: none;
  }
}

.header__top-btn {
  display: none;
}

@media only screen and (max-width: 960px) {
  .header__top-btn {
    display: inline-block;
    vertical-align: middle;
    float: left;
    margin-right: -60px;
  }
  .header__top-btn button {
    position: relative;
    display: inline-block;
    vertical-align: middle;
  }
  .header__top-btn button:not(:last-child) {
    margin-right: 10px;
  }
}

