.burger {
  display: inline-block;
  vertical-align: middle;
  width: 22px;
  height: 18px;
  background-size: cover;
  margin-top: -2px;
}

.mob-search {
  display: inline-block;
  vertical-align: middle;
  width: 22px;
  height: 21px;
  background-size: cover;
}

.logo {
  display: inline-block;
  vertical-align: middle;
}

.logo:hover {
  text-decoration: none;
}

.logo__description {
  display: block;
  font-size: 17px;
  color: #787f89;
  font-family: "Roboto-Light", Arial, Helvetica, sans-serif;
}

@media only screen and (max-width: 960px) {
  .logo__description {
    display: none;
  }
}

.logo img.logo__img {
  margin-bottom: 3px;
}

@media only screen and (max-width: 960px) {
  .logo img.logo__img {
    max-width: 180px;
  }
}

@media only screen and (max-width: 360px) {
  .logo img.logo__img {
    max-width: 150px;
  }
}

.lang {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  margin-right: 10px;
}

.lang:after {
  content: " ";
  display: table;
  clear: both;
}

@media only screen and (max-width: 960px) {
  .lang {
    position: absolute;
    right: 0;
    top: 12px;
  }
}

.lang.is-open .lang__wrap {
  display: block;
}

.lang__current {
  background: transparent;
}

.lang__current img {
  display: inline-block;
  vertical-align: middle;
  width: 25px;
  height: 25px;
  overflow: hidden;
  border-radius: 50%;
}

@media only screen and (max-width: 960px) {
  .lang__current img {
    float: right;
    margin-left: 5px;
  }
}

.lang__current span {
  display: inline-block;
  vertical-align: middle;
  font-size: 17px;
  color: #a7393a;
  font-family: "Roboto-Light", Arial, Helvetica, sans-serif;
}

.lang__arr {
  display: inline-block;
  vertical-align: middle;
  background: url("../assets/img/general/arr.png");
  background-size: cover;
  width: 14px;
  height: 9px;
  margin: 0 3px;
}

.lang__wrap {
  display: none;
  position: absolute;
  top: 0;
  background-color: #fff;
  border: 1px solid #dba960;
  z-index: 9999;
  border-radius: 9px;
  padding: 8px 10px 10px 11px;
  top: -3px;
  left: -12px;
  width: 200px;
  text-align: left;
}

@media only screen and (max-width: 960px) {
  .lang__wrap {
    left: auto;
    right: 0;
    width: 140px;
  }
}

